				"html": "<p>For software engineers, it is easy to reach agreement that good commit messages are important. It equally easy to reach agreement that most commit messages are not good. In this post, AWW, a formula  for writing basic meaningful commit messages, will be discussed.</p>\n<h2>3 Things Meaningful Commit Messages Should Have</h2>\n<ul>\n<li><strong>action</strong>: this word is a verb that describes what the commit does</li>\n<li><strong>what</strong>: word(s) that describe what the <em>action</em> is relative to</li>\n<li><strong>where</strong>: word(s) that describe <em>where</em> the <em>action</em> is happening to <em>what</em> </li>\n</ul>\n<p>This is called <strong>AWW</strong> which sounds cute so I like it.</p>\n<p>Here’s a git terminal example:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-bash\"><code>  <span class=\"token function\">git</span> commit -m <span class=\"token string\">'changes <span class=\"token variable\"><span class=\"token variable\">`</span>dataObj.id<span class=\"token variable\">`</span></span> in the <span class=\"token variable\"><span class=\"token variable\">`</span>dataObj<span class=\"token variable\">`</span></span>'</span>\n</code></pre>\n      </div>\n<p>In the example above:</p>\n<ul>\n<li><strong>action</strong> is the word ‘changes’</li>\n<li><strong>what</strong> is <code>dataObj.id</code></li>\n<li><strong>where</strong> is ‘in the <code>dataObj</code>’</li>\n</ul>\n<h2>Why make a formula?</h2>\n<p>Git messages are important. They seem simple to write in discussion. Yet, when changing multiple files or things in a file, the meaning of changes can be lost easily. Following the AWW formula helps composing meaning when committing. </p>",
